body {
	font-family: 'Archivo Black', sans-serif;
	background-color: #000;
	color: #eee;
	background-image: url('../src/img/bg.min.svg');
}

.App {
	text-align: center;
	padding: 2% 5%;
}

.App * {
	box-sizing: border-box;
}

ul {
	padding: 0;
	list-style: none;
}

p {
	margin: 5px 0;
}

.display-none {
	display: none;
}
.showSuggestions {
	display: block;
}

input[type='text'],
input[type='number'] {
	padding: 8px 6px 8px 10px;
	border-radius: 4px;
	outline: none;
	border: 1px solid #ccc;
	font-size: 14px;
	line-height: 20px;
	align-self: center;
	height: 38px;
}

.city {
	width: 300px;
}
.event-grid {
	margin-top: 10px !important;
}

.event-number {
	width: 70px;
	margin: 3px;
}
.event-number,
.city input[type='text'] {
	font-family: 'Roboto', sans-serif !important;
	font-size: 1.1rem !important;
	background: none !important;
	border: 1px rgb(229, 249, 9) solid !important;
	color: white;
}

input:focus {
	color: white !important;
}

.header-logo {
	width: 100%;
	height: 95px;
	background-position: center;
	background-repeat: no-repeat;
	margin: 20px 0 40px;
	background-image: url('../src/img/meetup-logo.svg');
}

.event-header {
	font-size: 2rem;
	font-weight: bold;
	padding: 10px 0 0 0;
	color: rgb(229, 249, 9);
	width: 100%;
}

.header-segment {
	background: none !important;
	border: none !important;
	box-shadow: none !important;
	padding: 5px !important;
}

.event-number-input {
	margin-top: 20px !important;
}

.grid-header {
	background: none !important;
	border-radius: 20px !important;
	margin-bottom: 30px;
}

.grid-row-header {
	align-items: center !important;
	justify-content: center !important;
}

.grid-header {
	width: 97%;
	margin: 0 auto 60px !important;
}

.CitySearch {
	display: flex;
	flex-direction: column;
	flex-basis: 300px;
	max-width: 300px;
	margin: auto;
	position: relative;
}

.suggestions {
	width: 300px;
	position: absolute;
	z-index: 2;
	align-self: center;
	margin: 0;
	top: 40px;
	font-size: 14px;
	box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
	text-align: left;
	padding: 8px 6px 8px 10px;
	background-color: rgb(0, 0, 0);
	cursor: default;
}

.suggestions > li:last-child {
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
	background-color: rgb(229, 249, 9);
	color: rgb(0, 0, 0);
}

.event-block {
	border: rgb(229, 249, 9) 3px solid !important;
	background: none !important;
	border-radius: 20px !important;
}

.event {
	padding: 10px 10px 35px;
	text-align: left;
	margin-bottom: 10px;
	border: none !important;
}

.event h2 {
	margin-bottom: 5px;
	font-family: 'Archivo Black', sans-serif !important;
}

.event > p {
	font-family: 'Roboto', sans-serif !important;
	font-size: 1.5rem !important;
}
.event a,
.event-details p {
	font-size: 1.15rem !important;
	font-family: 'Roboto', sans-serif !important;
}

.event-details,
.event-email {
	margin-top: 20px !important;
	padding-bottom: 30px;
}

.event-details p {
	margin-top: 10px;
}

.event-details a {
	text-decoration: underline;
	color: #01fff4;
}

.event-email a {
	opacity: 1 !important;
}

.details-btn {
	position: absolute;
	right: 15px;
	bottom: 20px;
	border-radius: 10px !important;
	font-family: 'Roboto', sans-serif !important;
}

.event-link {
	word-break: break-word;
}

.Alert {
	display: flex;
	justify-content: center;
}

.Alert p {
	position: absolute;
	padding: 10px;
	border-radius: 10px;
	background-color: #000;
	z-index: 1024;
	min-width: 425px;
	top: -55px;
	font-family: 'Roboto', sans-serif !important;
	font-size: 1.15rem !important;
	font-weight: bold;
}

.event-number-input .Alert p {
	top: 0px;
}

