@media only screen and (min-width: 992px) {
	.data-vis-wrapper {
		flex-direction: row;
	}
	.data-vis-wrapper .recharts-responsive-container {
		flex-basis: 50%;
		max-width: 50%;
		min-width: auto;
		margin: 0 auto;
	}
}

@media only screen and (max-width: 666px) {
	.event-header {
		font-size: 1.1rem !important;
	}
}
